import * as React from "react"
import ImageCard from "../components/ImageCard"
import "../styles/global.scss"
import { getImage, StaticImage } from "gatsby-plugin-image"
import { useStaticQuery, graphql } from "gatsby"
import styled from "styled-components"
import { Button, LastItem, Hr } from "../styles/Utility.styled"
import { Link } from "gatsby"
import Footer from "../components/Footer"
import Delimiter from "../components/Delimiter"


const CardWrapper = styled.div`
display:grid;
grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
grid-auto-flow: dense;
align-items: start;
gap: var(--m-2q);
`

const GaleriePage = () => {

  const data = useStaticQuery(graphql`
  query MyQuery {
    markdownRemark {
      frontmatter {
        work {
          id
          text1
          text2
          image {
            childImageSharp {
              gatsbyImageData(width: 400)
            }
          }
        }
        moreWork {
          id
          text1
          text2
          image {
            childImageSharp {
              gatsbyImageData(width: 400)
            }
          }
        }
      }
    }
  }`);
  const work = data.markdownRemark.frontmatter.work;
  const moreWork = data.markdownRemark.frontmatter.moreWork;

  const [showMore, setShowMore] = React.useState(false);


  return (
    <>
      <div className="bg-image-wrapper">
        <StaticImage className="bg-image" loading="eager" src="../images/forest_colorized.jpg" alt="" layout="fullWidth" />
        <div className="container padding-y-3 flex-column-center-end gap-m white" >
          <h1 className="page-title">Galerie</h1>
          <Hr />
          <h3 className="page-subtitle">APERÃ‡U DE NOTRE TRAVAIL</h3>
        </div>
         <Delimiter absolute bottom left /> 

      </div>
      <CardWrapper className="container padding-y" >
        {work.map((item, index) => {
          return <ImageCard key={index} text1={item.text1} text2={item.text2} image={getImage(item.image)} />
        })}

        {showMore && moreWork.map((item, index) => {
          return <ImageCard key={index} text1={item.text1} text2={item.text2} image={getImage(item.image)} />

        })}

        <LastItem>
          <Link to="/#images" className="button-2">Retour</Link>
          {!showMore && <button className="button-2" onClick={() => setShowMore(true)}>Voir plus</button>}
        </LastItem>




      </CardWrapper>
      <Footer />
    </>
  )
}

export default GaleriePage

export const Head = () => <title>Galerie</title>