import { GatsbyImage } from "gatsby-plugin-image"
import * as React from "react"
import styled from "styled-components"

const Card = styled.div`
display: flex;
flex-direction: column;
min-width: 5rem;
height: auto;
color: var(--text);

`

const TextWrapper = styled.div`
display: flex;
flex-direction: column;
justify-content: center;
align-items: flex-start;
padding: var(--space-s);
gap: calc(var(--space-s) * 0.5) ;
`
const Text1 = styled.p`
font-size: var(--step--2);
`
const Text2 = styled.p`
font-weight: 700;
line-height: 1;
`

export default function ImageCard({ text1, text2, background, image }) {
   
   const style = {
    background: background? background : "#eee"
}
    
    return (
        <Card style={style}>
            {
                (image && image !== null) ? <GatsbyImage image={image} alt="" /> : <p>no image</p>
            }
            <TextWrapper>

            <Text1>{text1? text1: "text1"}</Text1>
            <Text2>{text2? text2: "text2"}</Text2>
            </TextWrapper>
        </Card>
    )
}
//query


